.wrapper {
  font-family: 'Nunito Sans', 'Helvetica Neue', Helvetica, Arial, sans-serif;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  padding: 15px 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 1000px;
  margin: auto;
}

.img {
  display: inline-block;
  vertical-align: top;
}

.h1 {
  font-weight: 900;
  font-size: 20px;
  line-height: 1;
  margin: 15px 0 0px 10px;
  display: inline-block;
  vertical-align: top;
}

button + button {
  margin-left: 10px;
}
